.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 100;
}

.btn__link {
  position: relative;
  color: #fff;
  font-size: 0.875rem;
  line-height: 1.5714285714;
  padding: 1rem 5.5rem 1rem 2.5rem;
  background-color: #408F95;
  outline: 0.0625rem solid #408F95;
  outline-offset: -0.0625rem;
  z-index: auto;
  transition: 0.3s color, 0.3s background-color;
}

.btn__link::after {
  position: absolute;
  content: "";
  width: 12.625rem;
  height: 3.375rem;
  border: solid 0.0625rem #408F95;
  top: 0.25rem;
  left: 0.25rem;
  z-index: -1;
}

.btn__link:hover {
  background-color: #fff;
  color: #408F95;
}

.btn__link span {
  position: relative;
}

.btn__link span::after {
  position: absolute;
  content: "";
  width: 2.5rem;
  height: 0.0625rem;
  background-color: #fff;
  top: 50%;
  left: 5.125rem;
  transition: 0.3s background-color;
}

.btn__link:hover span::after {
  background-color: #408F95; /* 白色に変更 */
}

.btn__link span::before {
  position: absolute;
  content: "";
  width: 2.5rem;
  height: 0.5625rem;
  border-right: solid 0.0625rem #fff;
  transform: rotate(-45deg);
  top: 78%;
  left: 5.3125rem;
  transition: 0.3s border-right;
}

.btn__link:hover span::before {
  border-right: solid 0.0625rem #408F95;
}

